@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.homepage{background-image:url(/static/media/4.af8e30a8.jpg);height:100%;width:100%;margin-left:auto;margin-right:auto;background-repeat:no-repeat;background-size:100%;padding:0;position:absolute;top:0;text-align:center;justify-content:center;opacity:0.15;animation:bgZoomAnim 10s linear infinite}@keyframes bgZoomAnim{0%{transform:scale(1)}49%{transform:scale(1.1)}50%{transform:scale(1.1)}0%{transform:scale(1)}}.glassEffect{background:rgba(255,255,255,0.15);box-shadow:0 8px 32px 0 rgba(31,38,135,0.37);backdrop-filter:blur(5.5px);-webkit-backdrop-filter:blur(5.5px);border-radius:10px;border:1px solid rgba(255,255,255,0.18);width:80%;height:43vw;margin-left:10%;margin-top:3vw;position:fixed}.heading{animation:blinkAlign 4s linear infinite;position:relative;left:16%;padding:4% 4%;font-family:'Share Tech Mono', monospace;color:#120078;font-size:3.5vw;font-weight:bold;opacity:200%}.headingAnimation1{animation:blink 4s linear infinite}.headingAnimation2{position:relative;left:-20%;animation:blink 4s linear infinite;animation-delay:-2s}@keyframes blinkAlign{0%{left:20%}49%{left:20%}50%{left:16%}99%{left:16%}100%{left:20%}}@keyframes blink{0%{opacity:1}30%{opacity:1}35%{opacity:0.0}40%{opacity:0.6}42%{opacity:0}44%{opacity:0.5}45%{opacity:0}46%{opacity:0.5}47%{opacity:0}48%{opacity:0.5}49%{opacity:0}49.5%{opacity:0.5}50%{opacity:0}100%{opacity:0}}.profileimg{height:20%;width:20%;border-radius:50%;top:5vw;left:37.5%;position:relative}

.tile{height:12vw;position:relative;width:30%;border-style:groove;cursor:pointer;border-radius:10%}.bgTileImg{cursor:pointer;border-radius:10%;background-size:contain;height:100%;width:100%;position:absolute;opacity:1;left:0;top:0;transition:left 0.2s ease-in-out 0.2s, top 0.2s ease-in-out 0.1s;background:rgba(125,223,253,0.75);box-shadow:0 8px 32px 0 rgba(31,38,135,0.37);backdrop-filter:blur(5px);-webkit-backdrop-filter:blur(5px);border:1px solid rgba(255,255,255,0.18)}.topic{color:#120078;position:relative;font-size:5vw;font-family:'Share Tech Mono', monospace}.hoverleftdown{top:-1.5vw;left:-3.5vw;top:1.5vw;opacity:0.5}.hoverrightdown{top:1.5vw;left:3.5vw;opacity:0.5}.hoverleftup{left:-3.5vw;top:-1.5vw;opacity:0.5}.hoverrightup{top:-1.5vw;left:3.5vw;opacity:0.5}

.aboutme{position:absolute}

.header{width:100%;position:fixed;height:10%;min-height:30px;padding-top:1%;background:rgba(125,223,253,0.75);box-shadow:0 4px 16px 0 rgba(31,38,135,0.37);backdrop-filter:blur(5px);-webkit-backdrop-filter:blur(5px);border:1px solid rgba(255,255,255,0.18)}.nav-bar{display:inline;margin-top:2%;width:50%;position:relative;height:8%;left:35%}.nav-bar-item{margin:1px 2%;color:#120078;display:inline;font-size:3vw;font-family:'Share Tech Mono', monospace;text-decoration:none}.nav-bar-item:hover{opacity:1;color:#f6f6f6;text-decoration:underline}

